import React from 'react';
import {useParams} from "react-router-dom"
import OutpatientReportsByOrganizationContainer from "../containers/OutpatientReportsByOrganizationContainer";

const OutpatientReportsByOrganizationPage = () => {
    const {org_id} = useParams()
    return (
        <>
            <OutpatientReportsByOrganizationContainer  org_id={org_id}/>
        </>
    );
};

export default OutpatientReportsByOrganizationPage;