import React, {useState} from 'react';
import Title from "../../../components/title";
import GridView from "../../../containers/grid-view";
import {KEYS} from "../../../constants/keys";
import {URLS} from "../../../constants/urls";
import {get, round, debounce} from "lodash"
import downloadIcon from "../../../assets/icons/download.svg"
import {NumericFormat} from "react-number-format";
import cloudIcon from "../../../assets/icons/cloud.svg"
import monitorIcon from "../../../assets/icons/monitor.svg"
import femaleIcon from "../../../assets/icons/gender-female.svg";
import maleIcon from "../../../assets/icons/gender-male.svg";
import Input from "../../../components/input";
import {useTranslation} from "react-i18next";
import {useNavigate} from "react-router-dom";
import {usePostQuery} from "../../../hooks/api";
import {REPORT_TYPES} from "../../../constants";
import {OverlayLoader} from "../../../components/loader";

const columns = [
    {
        title: 'НАЗВАНИЕ (КОД)',
        key: 'name',
        render: ({value, row}) => <div className={'px-3 relative'}>{value} <NumericFormat
            className={'text-xs text-red-500 absolute -bottom-4 right-2'} displayType={'text'} thousandSeparator={""}
            value={get(row, 'base_rate_by_group', 0)}/></div>
    },
    {
        title: '<1',
        key: 'patient_groups.code',
        render: ({row}) => <div className={'text-left '}>
            <div
                className={'px-3  pb-2.5 border-b border-dashed border-b-[#006D85] '}>
                <span
                    className={'bg-[#D9D9D9] inline-block rounded-[20px] px-2 py-1 text-xs font-semibold text-black mr-14'}>Нас.</span>
                <NumericFormat
                    thousandSeparator={' '} displayType={'text'}
                    value={round(get(row, 'patient_groups[1].count', 0), 2)}/>
            </div>
            <div className={'pt-2.5 px-3 '}><span
                className={'bg-[#D9D9D9] inline-block rounded-[20px] px-2 py-1 text-xs font-semibold text-black mr-10 '}>Сумма.</span><NumericFormat
                className={''} thousandSeparator={' '} displayType={'text'}
                value={round(get(row, 'patient_groups[1].payment', 0), 2)}/>
            </div>
        </div>,
    },
    {
        title: '1-5',
        key: 'patient_groups',
        render: ({row}) => <div>
            <div
                className={'pb-2.5 border-b border-dashed border-b-[#006D85]'}><NumericFormat
                thousandSeparator={' '} displayType={'text'} value={round(get(row, 'patient_groups[2].count', 0), 2)}/>
            </div>
            <div className={'pt-2.5 '}><NumericFormat thousandSeparator={' '} displayType={'text'}
                                                      value={round(get(row, 'patient_groups[2].payment', 0), 2)}/>
            </div>
        </div>
    },
    {
        title: '6-14',
        key: 'patient_groups',
        render: ({row}) => <div>
            <div
                className={'pb-2.5 border-b border-dashed border-b-[#006D85]'}><NumericFormat
                thousandSeparator={' '} displayType={'text'} value={round(get(row, 'patient_groups[3].count', 0), 2)}/>
            </div>
            <div className={'pt-2.5 '}><NumericFormat thousandSeparator={' '} displayType={'text'}
                                                      value={round(get(row, 'patient_groups[3].payment', 0), 2)}/>
            </div>
        </div>
    },
    {
        title: <span className={'inline-flex items-center'}>15-39F <img src={femaleIcon} alt="female"/></span>,
        key: 'patient_groups',
        render: ({row}) => <div>
            <div
                className={'pb-2.5 border-b border-dashed border-b-[#006D85]'}><NumericFormat
                thousandSeparator={' '} displayType={'text'} value={round(get(row, 'patient_groups[4].count', 0), 2)}/>
            </div>
            <div className={'pt-2.5 '}><NumericFormat thousandSeparator={' '} displayType={'text'}
                                                      value={round(get(row, 'patient_groups[4].payment', 0), 2)}/>
            </div>
        </div>
    },
    {
        title: <span className={'inline-flex items-start'}>15-39M <img src={maleIcon} alt="male"/></span>,
        key: 'patient_groups',
        render: ({row}) => <div>
            <div
                className={'pb-2.5 border-b border-dashed border-b-[#006D85]'}><NumericFormat
                thousandSeparator={' '} displayType={'text'} value={round(get(row, 'patient_groups[5].count', 0), 2)}/>
            </div>
            <div className={'pt-2.5 '}><NumericFormat thousandSeparator={' '} displayType={'text'}
                                                      value={round(get(row, 'patient_groups[5].payment', 0), 2)}/>
            </div>
        </div>
    },
    {
        title: <span className={'inline-flex items-center'}>40-49F <img src={femaleIcon} alt="female"/></span>,
        key: 'patient_groups',
        render: ({row}) => <div>
            <div
                className={'pb-2.5 border-b border-dashed border-b-[#006D85]'}><NumericFormat
                thousandSeparator={' '} displayType={'text'} value={round(get(row, 'patient_groups[6].count', 0), 2)}/>
            </div>
            <div className={'pt-2.5 '}><NumericFormat thousandSeparator={' '} displayType={'text'}
                                                      value={round(get(row, 'patient_groups[6].payment', 0), 2)}/>
            </div>
        </div>
    },
    {
        title: <span className={'inline-flex items-start'}>40-49M <img src={maleIcon} alt="male"/></span>,
        key: 'patient_groups',
        render: ({row}) => <div>
            <div
                className={'pb-2.5 border-b border-dashed border-b-[#006D85]'}><NumericFormat
                thousandSeparator={' '} displayType={'text'} value={round(get(row, 'patient_groups[7].count', 0), 2)}/>
            </div>
            <div className={'pt-2.5 '}><NumericFormat thousandSeparator={' '} displayType={'text'}
                                                      value={round(get(row, 'patient_groups[7].payment', 0), 2)}/>
            </div>
        </div>
    },
    {
        title: '50-65',
        key: 'patient_groups',
        render: ({row}) => <div>
            <div
                className={'pb-2.5 border-b border-dashed border-b-[#006D85]'}><NumericFormat
                thousandSeparator={' '} displayType={'text'} value={round(get(row, 'patient_groups[8].count', 0), 2)}/>
            </div>
            <div className={'pt-2.5 '}><NumericFormat thousandSeparator={' '} displayType={'text'}
                                                      value={round(get(row, 'patient_groups[8].payment', 0), 2)}/>
            </div>
        </div>
    },
    {
        title: '65<',
        key: 'patient_groups',
        render: ({row}) => <div>
            <div
                className={'pb-2.5 border-b border-dashed border-b-[#006D85]'}><NumericFormat
                thousandSeparator={' '} displayType={'text'} value={round(get(row, 'patient_groups[9].count', 0), 2)}/>
            </div>
            <div className={'pt-2.5 '}><NumericFormat thousandSeparator={' '} displayType={'text'}
                                                      value={round(get(row, 'patient_groups[9].payment', 0), 2)}/>
            </div>
        </div>
    },
    {
        title: <div className={'flex justify-around'}><span className={'mr-10 ml-3'}>По группам </span><span
            className={'mr-3'}>По количеству</span></div>,
        key: 'organization_level',
        render: ({row}) => <div className={'text-center'}>
            <div
                className={"pb-2.5 border-b border-dashed border-b-[#006D85] relative after:absolute after:top-full after:content-[''] after:h-[50px]  after:border-r after:border-dashed after:border-r-[#006D85] after:w-[1px] after:left-1/2"}>
                <NumericFormat displayType={'text'} thousandSeparator={" "} value={get(row, 'total_count', 0)}/></div>
            <div className={'flex justify-around '}><span
                className={'pt-2.5 w-1/2 '}><NumericFormat displayType={'text'} thousandSeparator={" "}
                                                           value={round(get(row, 'total_payment_by_group', 0), 2)}/></span><span
                className={'pt-2.5 w-1/2 '}><NumericFormat displayType={'text'} thousandSeparator={" "}
                                                           value={round(get(row, 'total_payment', 0), 2)}/></span></div>
        </div>,
    },
    {
        title: 'За месяц',
        key: 'grand_total',
        render: ({row}) => <div>
            <div className={'pt-2.5 '}><NumericFormat thousandSeparator={' '} displayType={'text'}
                                                      value={round(get(row, 'grand_total', 0) / 12, 2)}/>
            </div>
        </div>
    },
    {
        title: 'Итого',
        key: 'grand_total',
        render: ({row}) => <div>
            <div className={'pt-2.5 '}><NumericFormat thousandSeparator={' '} displayType={'text'}
                                                      value={round(get(row, 'grand_total', 0), 2)}/>
            </div>
        </div>
    },
    {
        title: 'ДЕЙСТВИЯ',
        key: 'organization_level',
        classnames: '!text-center',
        render: ({row}) => <div className={'flex justify-around items-center  px-3'}>
            <img className={'mr-1'} src={cloudIcon} alt="cloud"/>
            <img src={monitorIcon} alt="cloud"/>
        </div>
    }

]
const OutpatientReportsByOrganizationContainer = ({
                                                      org_id = null,
                                                  }) => {
    const navigate = useNavigate()
    const {t} = useTranslation()
    const [name, setName] = useState('')
    const {mutate: generateReportRequest, isLoading: isLoadingReport} = usePostQuery({listKeyId: KEYS.reports})

    const generateReport = () => {
        generateReportRequest({
            url: `${URLS.reports}/${REPORT_TYPES.outpatient_by_organization}`,
            attributes: {
                filters: {
                    id: org_id
                }
            }
        }, {
            onSuccess: () => {
                navigate('/reports')
            }
        })
    }
    return (
        <div>
            <div className="grid grid-cols-12 items-center">
                <div className="col-span-8">
                    <Title>{t('Организации первичного звена')}</Title>
                </div>
                <div className="col-span-4 text-right">
                    <button
                        onClick={generateReport}
                        className={'inline-flex items-center py-2.5 pl-2.5 pr-5 text-sm font-bold text-primary border border-primary rounded-lg'}>
                        <img className={'mr-2.5'} src={downloadIcon} alt={'export'}/>
                        {t('Экспорт')}
                    </button>
                </div>
                <div className="col-span-12 mt-8">
                    {isLoadingReport && <OverlayLoader/>}
                    <GridView doubleRow filters={<div className={'grid grid-cols-12 mb-5 gap-x-8'}>
                        <div className="col-span-3">
                            <Input setValue={setName} value={name} label={t('Название')}/>
                        </div>
                    </div>} listKey={KEYS.outpatientReports}
                              url={URLS.outpatientReports + `/organizations/${org_id}`}
                              params={{name}}
                              columns={columns}/>
                </div>
            </div>
        </div>
    );
};

export default OutpatientReportsByOrganizationContainer;